@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src:
    local("PT Mono"),
    url("../fonts/pt-mono-regular.woff2") format("woff2"),
    url("../fonts/pt-mono-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed-Bold";
  font-style: normal;
  font-weight: 700;
  font-display: fallback;
  src:
    local("Roboto Condensed"),
    url("../fonts/roboto-condensed-700.woff2") format("woff2"),
    url("../fonts/roboto-condensed-700.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src:
    local("Roboto Condensed"),
    url("../fonts/roboto-condensed-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: fallback;
  src:
    local("Roboto"),
    url("../fonts/roboto-500.woff2") format("woff2"),
    url("../fonts/roboto-500.woff") format("woff");
}
